<template>
  <form @submit.prevent="submitEmployee" class="button-container" >
    <div class="input-field input-field-blue">
      <input type="text"
             id="name"
             v-model.trim="name"
             :class="{invalid: ($v.name.$dirty && !$v.name.required) || ($v.name.$dirty && !$v.name.minLength)}">
      <label for="name"></label>
      <small
        class="helper-text invalid"
        v-if="$v.name.$dirty && !$v.name.required"
      >
         
      </small>
      <small
        class="helper-text invalid"
        v-else-if="$v.name.$dirty && !$v.name.minLength"
      >
             {{$v.name.$params.minLength.min}} .
      </small>
    </div>

    <button class="btn-flat waves-effect waves-light auth-submit blue darken-1 white-text" type="submit"> </button>
  </form>
</template>

<script>
import { required, minLength } from 'vuelidate/lib/validators'

export default {
  name: 'AddCardEmployees',
  data () {
    return {
      name: ''
    }
  },
  validations: {
    name: { required, minLength: minLength(2) }
  },
  methods: {
    submitEmployee () {
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }

      if (this.name.trim()) {
        const newEmployee = {
          id: Date.now(),
          name: this.name,
          city: ''
        }

        this.$emit('add-employee', newEmployee)
        this.name = ''
      }
    }
  }
}
</script>

<style scoped>
.button-container {
  display: flex;
  align-items: center;
}

.button-container .input-field {
  margin-right: 30px;
}
</style>
